@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,400&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,600;1,200;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    margin:0;
}
.main{
    width: 50%;
    margin:0 auto;
}


.input-wrapper{
    /* position:relative; */
    max-width: 30vw;
    min-width: 30vw;
    min-height: 40vh;
    text-align: center;
        background-color: dodgerblue;
    padding: 50px 100px;
    box-shadow: 5px 10px 50px #888888;
    margin:20px auto;
}

input{
    min-width: 25vw;
    max-width: 25vw;
    min-height: 1rem;
    padding: 10px;
    border-color: white;
    font-size: 1.25em;
    border-radius: 5px;
}
input:focus{
    border: 0px;
    outline:none;
    border-radius: 5px;
}
.center-it{
    max-width: 50vw; 
    margin: 0 auto;
}


@media only screen and (max-width: 600px) {
    .task-list {
      font-size: .5rem;
    }
  }
.task-list{
    /* position: absolute; */
    max-width: 25vw;
    min-width: 20vw;
    line-height: 25px;
    display: flex;
    flex-direction: row;
    font-size: 1.5rem;
    padding: 1em 10px;
    text-align: left;
    color: #e1e1e1;
    background-color: #c23f4c;
    text-align: center;
    margin: 0 auto;
    margin-bottom:  5px;
    box-shadow: 5px 20px 50px #955412;
    border-radius: 5px;
    /* margin-left: 40px; */

}
.text-left{
    flex-basis: 90%;
    text-align: left;
}
.icon-right{
    cursor: pointer;
    flex-basis: 10%;
    justify-content: right;
    text-align: right;
}
.explanation{
    margin: 0 auto;
    padding:15px 25px;
}
.explanation p{
    font-weight: bold;
    margin:0 auto;
    max-width:45ch;
    text-align: left;
    font-family: 'Roboto', sans-serif;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.5em;
    
}
@media only screen and (max-width: 768px) {
    .header{
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
    }
    .name-part{
        margin-left:0 0 !important;
        padding-left: 0% !important;
        text-align: center;
        margin: 0 auto;
        margin-left: 0;
    }
    .name-part p{
        margin:0 auto;
        text-align: center;
    }
  }
  .header a{
      text-decoration: none;
      color: #000;
      color: initial;
  }
.header{
    display: flex;
    /* flex-wrap: wrap; */
    flex-direction: row;
    align-items: center;
    padding:5px 20px;
    margin:0;
    background-color: #111;
    color:#eee;
}
.header:hover{
    cursor: pointer;
}
.name-part h2{
    font-size: 2.5em;
    letter-spacing: 2px;
    text-shadow: 2px 2px 1px tomato;
    margin-bottom: 5px;
}
.name-part p{
    max-width: 60%;
    color:rgba(240,240,240,.8);
    line-height: 1.25em;
    margin-top: 5px;
    font-size: 1.25em;
    
}
.name-part{
    align-items: center;
    justify-content: center;
    flex-basis: 60%;
    padding:0 20px;
    margin:0 auto;
    /* margin:20px; */
}
.name-desc h1{
    margin-bottom: 0;
}
.name-desc p{
    color: rgba(40,40,40,.8);
    font-size: 1.25em;
}

.general-item{
    align-items: center;
    background-color: tomato;
    box-shadow: 0 10px 20px #c23f4c;
    border-radius: 10px;
    font-size: 1.5em;
    align-self: center;
    padding:5px 20px;
    margin:10px;
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 8px;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width:600px) {
    html{
        font-size: 8px;
    }
    .profile-page{
        flex-direction: column;
    }
    .name-desc{
        flex-direction: column;
    }
    .profile-pic{
        width: 5rem;
        height: 5rem;
    }
    .technologies-used{
        flex-direction: column;
    }
}

.internal-wrapper{
    display: flex;
    flex-direction: column;
    width: 75%;
    margin:0 auto;
    border-left: 10px;
    border-color: tomato;
    background-color: rgba(2,2,11,.8);
    box-shadow: 0 20px 50px black;
    color: white;
    background-image: linear-gradient(to right,#ac2934,rgb(355, 75, 90),rgb(355, 78, 56),rgb(230, 57, 70));
}

.special{
    color:rgb(224, 204, 21);
    text-transform: uppercase;
}
.footer{
    position: fixed;
    bottom: 0;
    height: 75px;
    width: 100%;
    background-color: rgb(17, 17, 17);
    color:#eee;
    vertical-align: middle;
}
.footer p{
    text-align: center;
    
}
.profile-page{
    display: flex;
    border-left: 10px;
    border-color: tomato;
}
.name-desc{
    /* box-shadow: 0 2px 10px white; */
    flex-basis: 65%;
    display: flex;
    flex-direction: column;
    justify-self: center;
    text-align: center;
    border-left: 10px;
    border-left-color: tomato;
    justify-content: center;
}
.name-desc p.near-heading{
    color: rgba(250, 250, 250,.9);
    font-weight: light;
    max-width: 350px;
    margin-top: 5px;
    margin-bottom: 25px;
    font-size: medium;
    
}

.near-heading{
    margin:0 auto;
}
.profile-pic{
    
    display: flex;
    justify-self: center;
    align-self: center;
    flex-basis: 35%;
    justify-content: center;
    text-align: center;
    margin:0 auto;
    padding:25px 25px;
}
div img{
    display: block;
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
}
.wrapper{
    display: flex;
    flex-direction: column;
}
p.resume-desc{
    font-family: 'Times New Roman', Times, serif;
    color: rgba(255,255,255);
    max-width: 65%;
    min-width: 55%;
    justify-content: center;
    margin: 0 auto;
    line-height: 30px;
}
.technologies-used{
    display: flex;
    min-height: 250px;
    align-items: center;
    display: flex;
    justify-content: center;
}
.technologies{
    text-align: center;
    flex-basis: 35%;
    justify-self: center;
    margin: 0 auto;
}
.tech-item{
    justify-content: center;
    display: inline-block;
    margin: .5rem .5rem;
    padding :15px;
    border: 3px solid white;
    border-radius: 50px;
}
.tech-vals{
    flex-basis: 65%;
    justify-content: center;
    text-align: center;
   
}

.tech-vals > ul{
    padding-left: 0;
}
